/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-family: "Roboto", sans-serif;
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  border: 2px solid transparent;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3.1rem;
}

h3 {
  font-size: 2.8rem;
}

h4 {
  font-size: 2.3rem;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.content-center {
  max-width: 800px;
}

.align-right {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.brown-button {
  background-color: #DDA15E;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  transition: all 0.1s;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.brown-button:hover {
  background-color: #d48834;
}

.green-button {
  background-color: #36A543;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  transition: all 0.1s;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.green-button:hover {
  background-color: #297f33;
}

.red-button-block {
  display: block;
  width: 50%;
  background-color: #da0f2a;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  transition: all 0.1s;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.red-button-block:hover {
  background-color: #aa0c21;
}

.green-button-block {
  display: block;
  width: 50%;
  background-color: #36A543;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  transition: all 0.1s;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.green-button-block:hover {
  background-color: #297f33;
}

.main {
  background-image: url("../../src/img/header2.jpg");
}

.logo {
  max-width: 160px;
  transition: all 0.2s ease-in-out;
}
.logo:hover {
  color: #DDA15E;
  border: 1px solid #DDA15E;
  padding: 5px;
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
  color: #ffffff;
}
.header.main {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.main {
    min-height: 50rem;
  }
}
@media (min-width: 768px) {
  .header.main .header-container {
    height: 45rem;
  }
}

.header-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-container h1 {
  text-align: left;
  font-weight: 600;
  max-width: 50rem;
  line-height: 4rem;
}

.footer-container {
  padding: 2.5rem;
}

.copyright {
  color: #ffffff;
  text-align: center;
  font-size: 1.5rem;
}

#footer {
  background-color: #333333;
  padding: 2.5rem 0;
  margin-bottom: 0;
}

.bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navbar {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s ease-in;
}
@media (min-width: 768px) {
  .navbar {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navbar a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1.8rem;
  display: block;
  text-align: center;
  transition: color 0.2s;
}
.navbar a:hover {
  color: #DDA15E;
  border-bottom: 2px solid #DDA15E;
}
@media (min-width: 768px) {
  .navbar a {
    margin-right: 2rem;
  }
  .navbar a:last-of-type {
    margin-right: 0;
  }
}

#nav-footer {
  display: flex;
  flex-direction: column;
  visibility: visible;
  opacity: 1;
}
@media (min-width: 768px) {
  #nav-footer {
    flex-direction: row;
    text-align: center;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 4rem;
}

.show {
  visibility: visible;
  opacity: 1;
}

@media (min-width: 768px) {
  .icons-aboutus {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 7.5rem;
}
.icon h3 {
  text-transform: uppercase;
}
.icon p {
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .rent-container {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.rent {
  background-color: #ebebeb;
  border: 1px solid #333333;
  box-shadow: 5px 5px 10px #aeaeae;
  margin-top: 20px;
}
.rent .rent-container {
  padding: 2rem;
  gap: 0;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.rent h4, .rent p {
  margin: 0;
}
.rent p {
  font-size: 1.8rem;
}

.prize {
  color: #36A543;
  font-weight: 600;
  font-size: 2.5rem;
}

.icons-rent {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 30rem;
}
.icons-rent li {
  flex: 1;
  display: flex;
}
.icons-rent li img {
  flex: 0 0 2rem;
  margin-right: 1rem;
}
.icons-rent li p {
  font-weight: 800;
  color: #000000;
}

.contact-mainpage {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url("../../src/img/contact1.jpg");
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
}
.contact-mainpage h3 {
  color: #ffffff;
  font-weight: 500;
  max-width: 80rem;
  margin: 40px 30px 10px 30px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
}

@media (min-width: 768px) {
  .blog-mainpage {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 5rem;
    margin-top: 50px;
    margin-bottom: 50px;
  }
  .blog-mainpage h3 {
    margin-top: 10px;
  }
}
.blog-entry {
  margin-bottom: 2rem;
  margin-top: 7rem;
}
@media (min-width: 768px) {
  .blog-entry {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
    margin-top: 0;
  }
}
.blog-entry:last-of-type {
  margin-bottom: 0;
}

.blog-text {
  display: flex;
  flex-direction: column;
}
.blog-text a {
  color: #000000;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .blog-text a {
    margin-top: -1rem;
  }
}
.blog-text h4 {
  margin: 0;
}
.blog-text h4::after {
  content: "";
  display: block;
  width: 14rem;
  height: 0.5rem;
  background-color: #36A543;
  margin-bottom: 1rem;
}
.blog-text span {
  color: #e08709;
  font-weight: 600;
}
.blog-text p {
  margin: 0;
  font-size: 1.7rem;
}

.blog-weather {
  border: 1px solid #caf0f8;
  color: #caf0f8;
  border-radius: 20px;
  background-color: #06284d;
  margin-top: 50px;
  box-shadow: inset 0px 0px 10px rgba(255, 255, 255, 0.596);
}
@media (min-width: 768px) {
  .blog-weather {
    margin-top: 0;
  }
}
.blog-weather img {
  width: 12rem;
}

.weather-box {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: -10px;
}
.weather-box .temperature {
  color: #caf0f8;
  font-size: 4rem;
  font-weight: 700;
  margin: 0;
}
.weather-box .description {
  color: #caf0f8;
  font-size: 2rem;
  text-transform: capitalize;
  margin: 0;
}

.weather-description {
  display: flex;
  justify-content: space-around;
  margin-top: 30px;
  flex-wrap: wrap;
}
.weather-description p,
.weather-description span {
  font-family: "Roboto", sans-serif;
  color: #caf0f8;
  font-size: 1.6rem;
}
.weather-description img {
  width: 3rem;
}
.weather-description .humidity-box,
.weather-description .wind-box {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 7px;
}

.weather-notFound {
  width: 100%;
  text-align: center;
  margin-top: 50px;
  scale: 0;
  opacity: 0;
  display: none;
}
.weather-notFound img {
  width: 70%;
}
.weather-notFound p {
  color: #caf0f8;
  font-size: 2rem;
  margin-top: 12px;
}

.form {
  width: 80%;
  margin: 0 auto;
}
.form fieldset {
  margin-top: 2rem;
}
.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 0;
}
.form legend {
  font-size: 2rem;
  color: #e08709;
  font-weight: 700;
}
.form label {
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  border-radius: 5px;
  border: 1px solid #e1e1e1;
  margin-bottom: 1.5rem;
}
.form .contact-form {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#phone-contact,
#email-contact {
  margin: 0;
}

@media (min-width: 768px) {
  .aboutUs-container {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.aboutUs-text p {
  font-size: 1.8rem;
}

.thanks-to {
  display: flex;
}
@media (min-width: 768px) {
  .thanks-to {
    display: flex;
  }
}
.thanks-to .photo-box {
  width: 50%;
  text-align: center;
}
.thanks-to p {
  font-size: 1.6rem;
  margin: 0;
}
.thanks-to a {
  color: #e08709;
}

.alert {
  padding-left: 10px;
  opacity: 0.8;
  font-weight: 700;
}
.alert.error {
  background-color: #f3ccd2;
  border-left: 2rem solid #da0f2a;
}
.alert.success {
  background-color: #a4f0ad;
  border-left: 2rem solid #36A543;
}
.alert.error:first-of-type {
  margin-top: 20px;
}

table.properties {
  margin-top: 3rem;
  width: 100%;
  border-spacing: 0;
}
table.properties thead {
  background-color: #333333;
}
table.properties thead th {
  color: #ffffff;
  padding: 0.5rem;
}
table.properties .table-image {
  width: 10rem;
}
table.properties td {
  text-align: center;
}

.small-image {
  width: 20rem;
  margin-bottom: 1.5rem;
}